<template>
  <div class="search-header">
    <div class="sear w10">
      <div class="text-msg">
        <input class="search" type="text" placeholder="" value="message" v-model="message" @click="clear" ref="type1">
      </div>
      <router-link to="/">
        <a class=""><span></span></a>
      </router-link>
    </div>
    <div class="search-content">
      <div class="hot" v-show="show">
        <h3></h3>
        <ul>
          <li></li>
          <li></li>
          <li></li>
          <li></li>
          <li></li>
          <li></li>
          <li></li>
        </ul>
      </div>
      <div class="history" v-show="show">
        <h3 class="clearfix">
          <span class="fl"></span>
          <span class="fr"></span>
        </h3>
        <ul>
          <li>KTV</li>
          <li></li>
          <li></li>
          <li></li>
          <li></li>
          <li></li>
          <li></li>
          <li></li>
        </ul>
      </div>
      <ul class="search-list" v-show="sw">
        <router-link to="/categorylist"><li></li></router-link>
        <router-link to="/categorylist"><li></li></router-link>
        <router-link to="/categorylist"><li></li></router-link>
        <router-link to="/categorylist"><li></li></router-link>
        <router-link to="/categorylist"><li></li></router-link>
        <router-link to="/categorylist"><li></li></router-link>
        <router-link to="/categorylist"><li></li></router-link>
        <router-link to="/categorylist"><li></li></router-link>
        <router-link to="/categorylist"><li></li></router-link>
        <router-link to="/categorylist"><li></li></router-link>
        <router-link to="/categorylist"><li></li></router-link>
        <router-link to="/categorylist"><li></li></router-link>
      </ul>
    </div>
  </div>
</template>

<script>
    export default {
        name: "search-header",
        data(){
            return {
              message:"",
              show:true,
              sw:false
            }
        },
        methods:{
          clear(){
            return this.message = "";
          },
          change(){
            this.show = false;
            this.sw = true;
          }
        },
      watch:{
          message:{
            handler:function(val,old){
              if(val != old){
                this.show = false;
                this.sw = true;
              }
            }
          }
      }
    }
</script>

<style scoped>
  @import "../../assets/css/search/search-h.css";
  @import "../../assets/css/search/search-c.css";
</style>



